.coaches__title {
  margin: 0 0 48px;

  @include vp-1199 {
    margin: 0 0 31px;
  }

  @include vp-767 {
    margin: 0 0 50px;
  }
}

.coaches__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 40px;

  @include vp-1199 {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    padding: 0 56px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    padding: 0 32px;
  }
}
